.social-icons a {
  display:inline-block;
  height:2.4rem;
  width:2.4rem;
  background-color:transparent;
  color:#043361!important;
  border-radius:100%;
  text-align:center;
  font-size:1.15rem;
  line-height:2.5rem;
  margin-right:1rem;
}

.social-icons a:last-child{margin-right:0}.social-icons a:hover{
  background-color:white;
  color:black;
}

email {
    font-size: 14px;
    font-family: monospace;
}

autocolor {
  color:#595959;
}

.image.avatar img {
  width: 80%;
  padding: 20px;
  padding-top: 0px;
}

h1, h2, h3 {
  line-height: 1.1;
}

h1 {
  font-weight: 500;
  color: #043361;
  margin:0 0 20px;
  font-size: 28px;
}

h2 {
  color:#043361;
  font-weight: 500;
  margin:2px 0px 15px;
  font-size: 157%;
}

p, ul, ol, table, pre, dl {
  margin:0 0 20px;
}

a {
  color:#39c;
  text-decoration:none;
}

a:hover {
  color:#069;
}

strong {
  color:#222;
  font-weight:500;
}

small {
  font-size:11px;
}

.wrapper {
  width:960px;
  margin:0 auto;
}

header {
  width:300px;
  float:left;
  position:fixed;
  -webkit-font-smoothing:subpixel-antialiased;
  padding-top:4.0em;
  text-align:center;
}

@media all and (max-height: 650px) {
  header {
      position: absolute;
      height: auto;
  }
}

section {
  width:600px;
  float:right;
  padding-top:4.0em;
  padding-bottom:50px;
}

@media print, screen and (max-width: 960px) {

  div.wrapper {
    width:auto;
    margin:0;
  }

  header, section, footer {
    float:none;
    position:static;
    width:auto;
  }

  section {
    border:1px solid #e5e5e5;
    border-width:1px 0;
    padding:20px 0;
    margin:0 0 20px;
  }

  .image.avatar img {
    width: 117px;
    padding: 20px;
  }
  
  body {
    word-wrap:break-word;
    padding:15px;
  }

  header {
    padding: 0;
  }
}

@media print, screen and (max-width: 480px) {

  .image.avatar img {
    width: 95px;
    padding: 20px;
  }

}

@media print {
  body {
    padding:0.4in;
    font-size:12pt;
    color:#444;
  }
}

.publications, .projects {
  margin-top: 1.3rem;
  margin-bottom: 0;
  list-style: none;
  padding: 0;
}

.publications li .title {
  font-weight: bolder;
}

.publications li .pdf {
  font-size: 10px;
  color: #000000;
  border: 1px solid #000000;
  margin-left: 0.5rem;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
}

.projects li .author {
  font-style: italic;
}

.projects li .picture {
  display: flex;
}

.projects li .picture img {
  width: 30%;
  float: left;
}

.projects li .picture .description {
  padding-left: 20px;
  float: left;
}

